<template>
  <div id="backgroundImage" class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="5">
          <img class="navbar-brand-full justify-content-center" src="tout-paie-web/img/brand/logoImg.png" width="350" height="80" style="display: block;margin-left: auto;margin-right: auto;margin-bottom: 13%;">
          <b-card-group>
            <b-card no-body class="p-3">
              <b-card-body class="card-padding">
                <b-form @submit.stop.prevent="showLoader=true,forgotPassword()">
                  <h1>{{$t('Forgot Password')}}</h1>
                  <p class="text-muted">{{$t('Password will be send on registered email id')}}</p>
                  <b-input-group class="mb-3">
                    <b-input-group-prepend><b-input-group-text><i class="icon-user"></i></b-input-group-text></b-input-group-prepend>
                    <b-form-input type="email" v-model="userName" class="form-control" :placeholder="$t('Username')" required />
                  </b-input-group>
                  <b-input-group class="mb-3">
                  </b-input-group>
                  <b-row>
                    <b-col cols="12">
                      <b-button style="background-color: #ef402b;border-color: #C33421;color: white;margin-right: 6px;" v-if="showLoader==false" type="submit" class="px-4">Reset</b-button>
                       <b-button variant="primary" v-if="showLoader==true">
                        <b-spinner small type="grow"></b-spinner>
                        Loading...
                      </b-button>
                      <b-button style="color: #3B3B3B" to="/login" class="px-4">Cancel</b-button>
                    </b-col>
                    <b-col cols="6" class="text-right">
                    </b-col>
                  </b-row>
                </b-form>
              </b-card-body>
            </b-card>
          </b-card-group>
        </b-col>
      </b-row>
    </div>
  </div>
</template>

<script>

import * as toutPaieService from "./ToutPaieService.js";
import VueNotifications from 'vue-notifications'

export default {
   name: 'ForgotPassword',

  data: () => {
    return {
      user: "",
      userName: '',
      password: '',
      showLoader:false
    }
  },
   methods: {
     forgotPassword: function(){
         this.user = this.userName;
         
         var form = new FormData();
     
      form.append("Email", this.user);
      form.append("From", "W");


        toutPaieService.forgotPassword(form).then((res) => {
       
          let msg=res.message;

          if(msg == 'email_sent_msg') {
            this.showLoader=false;
              // VueNotifications.success({title: this.translateString('Success'), message: res.message});
              VueNotifications.success({message:this.translateString("You_will_recieve_Reset_Password_Link_on_your_mail_ID"),timeout:2500});
              this.$router.push('/login');
          } else {
            this.showLoader=false;
              // VueNotifications.error({title: this.translateString('Error'), message: res.message})
               VueNotifications.error({message: this.translateString(msg) ,timeout:2500});
          }
        });
    },
    translateString : function(value) {
      return this.$i18n.t(value);
    },
  },
  beforeMount() {
    console.log("forgot password");
  }
}
</script>